* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.app {
    background: url('./images/bg.jpeg');
    object-fit: cover;
    color: white;
    font-family: 'Work Sans', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}


.nav-bar {
    width: 100vw;
    height: 50px;
    background: rgb(65, 11, 51);
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-list {
    width: 1020px;
    list-style: none;
    display: flex;
    justify-content: flex-start;
}

.nav-link {
    padding: 5px;
    text-decoration: none;
    font-size: 20px;
    color: white;
    transition: 0.5s;
}

.nav-link:hover {
    color: red;
}



.footer {
    display: flex;
    justify-content: center;
    font-size: 10px;
}

.GroupedTeamMember {
    display: flex;
    justify-content: center;
}


.not {
    display: flex;
    justify-content: center;
    height: 100vh;
}

.G-container {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
}

.G-card {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 25px;
    padding: 10px;
    align-items: center;
    border: 2px solid black;
    width: 400px;
    height: 500px;
    border: 2px solid white;
}

.G-card .collapsed {
    display: none;
}

.member {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 10px 0;
    border: 2px solid white;
}