* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.card-collection {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

select {
    width: 800px;
    height: 40px;
    background: transparent;
    color: white;
    font-size: 30px;
    border: 2px solid white;
    border-radius: 5px;
    text-align: center;
}

option {
    background-color: black;
    cursor: pointer;
}

.card {
    display: flex;
    flex-direction: column;
    margin: 1rem;
    padding: 0.5rem;
    border: solid 5px red
}

.card-normal {
    display: flex;
    flex-direction: column;
    margin: 1rem;
    padding: 0.5rem;
    border: solid 0.5px black;
    gap: 10px;
}


.container {
    display: flex;
    justify-content: center;
    width: 1020px;
}

.col {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

.card-standout {
    box-shadow: 2px;
}

.card-normal {
    box-shadow: 0px;

}

.card-body {
    display: flex;
    flex-direction: column;
    gap: 10px;
}